<template>
    <div>
        <Form ref="formValidatename" :model="formValidate" :rules="ruleValidate" :label-width="115">
        <FormItem label="" prop="value1">
            <Cascader :data="formValidate.data" @on-change="setvalues" v-model="formValidate.value1"></Cascader>
        </FormItem>
        <FormItem label="" prop="number">
            <Input v-model="formValidate.number" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="Ownertype">
            <RadioGroup v-model="formValidate.Ownertype">
                <Radio label="Internalcompany"></Radio>
                <Radio label="Internalstaff"></Radio>
                <Radio label="client"></Radio>
                <Radio label="supplier"></Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="" prop="Owner">
            <!-- <Input v-model="formValidate.mail" placeholder=""></Input> -->
            <Select v-model="formValidate.Owner" >
                <Option v-for="item in cityList" :value="item.value1" :key="item.value1">{{ item.label1 }}</Option>
            </Select>
        </FormItem>
         <FormItem label="" prop="Personcharge">
            <Input v-model="formValidate.city" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="lease" v-if="formValidate.isleas==true">
            <RadioGroup v-model="formValidate.lease">
                <Radio label="yes"></Radio>
                <Radio label="no"></Radio>
            </RadioGroup>
        </FormItem>
        <!-- <FormItem label="" prop="goods">
            <RadioGroup v-model="formValidate.goods">
                <Radio label="male"></Radio>
                <Radio label="female"></Radio>
            </RadioGroup>
        </FormItem> -->
        <FormItem label="" prop="interest">
            <CheckboxGroup v-model="formValidate.interest">
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
            </CheckboxGroup>
        </FormItem>
        <FormItem label="" prop="desc">
            <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..."></Input>
        </FormItem>
        <FormItem>
            <!-- <Button type="primary" @click="handleSubmit('formValidate')">Submit</Button>
            <Button @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button> -->
        </FormItem>
    </Form>
    </div>
</template>
<script>
    export default {
        name:"formlocation",
        props:["formValidate","ruleValidate"],
        data () {
            return {
                
                formValidatename:this.formValidate,
                istrue:false,
                model1:"",
                cityList:[
                    {
                        value1: 'beijing',
                        label1: ''
                    },
                    {
                        value1: 'shenzhen',
                        label1: ''
                    },
                    {
                        value1: 'xiangang',
                        label1: ''
                    },
                    {
                        value1: 'shanghai',
                        label1: ''
                    },
                ],
                Ownertype:"",
            }
        },
        methods: {
            handleSubmit () {
                this.$refs.formValidatename.validate((valid) => {
                    if (valid) {
                        this.$Message.success('Success!');
                        this.istrue=true;
                    } else {
                        this.$Message.error('');
                         this.istrue=false;
                    }
                    console.log(valid)
                })
            },
            handleReset () {
                this.$refs.formValidatename.resetFields();
                // this.$refs.formValidatename.value1="";
                // console.log(this.formValidatename)
            },
            setvalues(value){
                this.formValidatename.value1=value;
                console.log(this.formValidatename.value1)
            }
        }
    }
</script>


